import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileEmailAttachListResponse } from "../../Model/Response/FileEmailAttachListResponse";
import { FileEmailAttachResponse } from "../../Model/Response/FileEmailAttachResponse";
import { FileEmailAttachViewModel } from '../../Model/ViewModel/FileEmailAttachViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileEmailAttachServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileEmailAttachServicesProvider extends FileServicesProvider {
  private FileEmailAttachs: FileEmailAttachListResponse;
  private FileEmailAttach: FileEmailAttachResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileEmailAttach";
    console.log("Hello FileEmailAttachServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,order_type:boolean) {
    var sub_url: string;

      sub_url = "FileEmailAttach?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);

    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileEmailAttachs = new FileEmailAttachListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileEmailAttachs;
    });
  }



  public PostPutAsync(FileEmailAttachViewModel:FileEmailAttachViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileEmailAttach";
    else
    sub_url = "FileEmailAttach/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileEmailAttachViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileEmailAttachViewModel,mode).map(
      item => {
        this.FileEmailAttach = new FileEmailAttachResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileEmailAttach;
      }
    );
  }
}



